<template>
  <section>
    <el-col class="toolbar" style="padding-bottom: 0px;margin-top: 10px">
      <el-form :inline="true" size="mini">
        <el-form-item style="width: 100px">
          <el-select value="全部状态" v-model="selectStatus" placeholder="请选择" @change="statusChange">
            <el-option v-for="item in allStatus" :key="item.value" :label="item.label" :value="item.value"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item style="width: 100px">
          <el-select value="全部状态" v-model="selectType" @change="typeChange" placeholder="请选择">
            <el-option v-for="item in allStatus" :key="item.value" :label="item.label" :value="item.value"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item >
          <el-date-picker style="width: 120px; font-size: 10px"
                          v-model="startDate"
                          type="date"
                          placeholder="开始时间"
                          :picker-options="pickerStart">
          </el-date-picker>
        </el-form-item>
        <el-form-item>
          <el-date-picker style="width: 120px; font-size: 10px"
                          v-model="endDate"
                          type="date"
                          placeholder="结束时间"
                          :picker-options="pickerEnd">
          </el-date-picker>
        </el-form-item>
        <el-form-item>
          <el-input suffixIcon="el-icon-search" v-model="searchContent" :maxlength="10" placeholder="车牌/车型/手机/备注"
                    @keyup.enter.native="startSearch()"></el-input>
        </el-form-item>
      </el-form>
    </el-col>

    <el-table :data="orders" highlight-current-row v-loading="listLoading" @selection-change="selsChange" border
              stripe
              style="width: 100%; font-size: 10px">
      <el-table-column prop="id" label="序号" width="100" sortable></el-table-column>
      <el-table-column prop="state" label="项目" width="100"></el-table-column>
      <el-table-column prop="carType" label="车型" width="100"></el-table-column>
      <el-table-column prop="cash" label="总金额" width="100"></el-table-column>
      <el-table-column prop="createTime" label="开单时间" width="100"></el-table-column>
      <el-table-column prop="payTime" label="结算时间" width="100"></el-table-column>
      <el-table-column prop="finishTime" label="订单结束时间" width="100"></el-table-column>
      <el-table-column prop="fromWhere" label="来源" width="100"></el-table-column>
      <el-table-column prop="notice" label="备注" width="100"></el-table-column>
    </el-table>
  </section>
</template>

<script>
  import ElFormItem from "../../../node_modules/element-ui/packages/form/src/form-item.vue";
  import ElInput from "../../../node_modules/element-ui/packages/input/src/input.vue";

  export default {
    components: {
      ElInput,
      ElFormItem
    },
    methods:{
      startSearch(){
        if(this.searchContent == ""){
          alert("请输入内容")
        }else {
          alert("没有更多了")
        }

      },
      statusChange(){
        this.orders = [
          {
            id: "1",
            project: "",
            projectName: "",
            state: this.allStatus[this.selectStatus].label,
            carType: "宝马x5",
            cash: 20.0,
            createTime: "2018-10-12 14:30:20",
            payTime: "2018-10-12 15:30:20",
            finishTime: "2018-10-16 14:30:20",
            fromWhere: "移动端",
            notice: "无",
          },
          {
            id: "2",
            project: "",
            projectName: "",
            state: this.allStatus[this.selectStatus].label,
            carType: "奥迪Q7",
            cash: 20.0,
            createTime: "2018-10-12 15:20:20",
            payTime: "2018-10-12 16:30:20",
            finishTime: "2018-10-16 40:30:20",
            fromWhere: "移动端",
            notice: "物",
          },
        ]

      },
      typeChange(){

      }
    },
    data() {
      return {
        searchContent: "",
        selectStatus: "全部状态",
        selectType: "全部类型",
        allStatus: [
          {
            value: 0,
            label: "已开单"
          },
          {
            value: 1,
            label: "已结算"
          },
          {
            value: 2,
            label: "已提车"
          },
          {
            value: 3,
            label: "已取消"
          },
        ],

        allType: [
          {
            value: 0,
            label: "已开单"
          },
          {
            value: 1,
            label: "保养"
          },
          {
            value: 2,
            label: "机修"
          },
          {
            value: 3,
            label: "保险理赔"
          },
        ],
        startDate: new Date(),
        endDate: new Date(),
        pickerStart: {
          disabledDate(time) {
            return time.getTime() > Date.now();
          }
        },
        pickerEnd: {
          disabledDate(time) {
            return time.getTime() > Date.now() - 8.64e6;
          }
        },
        orders: [
          {
            id: "1",
            project: "",
            projectName: "",
            state: "已开单",
            carType: "宝马x5",
            cash: 20.0,
            createTime: "2018-10-12 14:30:20",
            payTime: "2018-10-12 15:30:20",
            finishTime: "2018-10-16 14:30:20",
            fromWhere: "移动端",
            notice: "无",
          },
          {
            id: "2",
            project: "",
            projectName: "",
            state: "已提车",
            carType: "奥迪Q7",
            cash: 20.0,
            createTime: "2018-10-12 15:20:20",
            payTime: "2018-10-12 16:30:20",
            finishTime: "2018-10-16 40:30:20",
            fromWhere: "移动端",
            notice: "物",
          },
          {
            id: "3",
            project: "",
            projectName: "",
            state: "已开单",
            carType: "宝马x5",
            cash: 20.0,
            createTime: "2018-10-12 14:30:20",
            payTime: "2018-10-12 15:30:20",
            finishTime: "2018-10-16 14:30:20",
            fromWhere: "移动端",
            notice: "无",
          },
          {
            id: "4",
            project: "",
            projectName: "",
            state: "已取消",
            carType: "凯蒂拉克",
            cash: 20.0,
            createTime: "2018-10-12 15:20:20",
            payTime: "2018-10-12 16:30:20",
            finishTime: "2018-10-16 40:30:20",
            fromWhere: "移动端",
            notice: "物",
          },
        ]
      }
    }
  }
</script>



